<template>
  <div>
    <div v-if="!Number.isFinite(level)">
      {{level}}
    </div>
    <v-row v-if="Number.isFinite(level)" align="center" style="justify-content: center">
      <v-col align-self="center" style="justify-content: center">
        <div
          style="
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
          "
        >
          <div
            v-for="n in 4 - level"
            :key="'white' + n"
            style="width: 100px; height: 30px"
          >
            <div
              :style="{ 'border-top': 4 - n < 3 ? 0 : 1 }"
              style="
                background-color: white;
                border: solid black 1px;
                width: 100%;
                height: 100%;
              "
            ></div>
          </div>
          <div v-for="n in level" :key="n" style="width: 100px; height: 30px">
            <div
              :style="{
                'background-color': levelColor[level - n],
                'border-top': level - n < 3 ? 0 : 1,
              }"
              style="border: solid black 1px; width: 100%; height: 100%"
            ></div>
          </div>
          <div
            style="
              display: flex;
              flex-direction: row;
              justify-content: center;
              align-items: center;
            "
          >
            {{ levels[level - 1] }}
          </div>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
export default {
  name: "Quartile",
  props: {
    /*
        四分位级别
         */
    level: {
      default: 0,
    },
  },
  data() {
    return {
      levels: ["低", "较低", "较高", "高"],
      levelColor: ["#BBDEFB", "#90CAF9", "#64B5F6", "#42A5F5"],
    };
  },
};
</script>

