import { axios } from "./request";
import { URL } from "./base_url";

export function getFactorInfo(id) {
    return axios({
        url: `${URL}/compare/${id}`,
        method: "get",
    });
}

export function getFactor(factor,id) {
    return axios({
        url: `${URL}/factor/${factor}/${id}`,
        method: "get",
    });
}
