<template>
  <div>
    <v-row align="center" style="justify-content: center; margin: 1%">
      <v-card width="100%">
        <v-card-title class="text-h5">
          {{ $store.state.item._stock_name }}
        </v-card-title>
        <v-card-subtitle
          style="
            display: flex;
            flex-direction: row;
            justify-content: end;
            align-items: end;
          "
          v-if="
            info.time === null || info.time === undefined || info.time === ''
          "
        >
          取值时刻:{{ selected }}
        </v-card-subtitle>
        <v-divider></v-divider>
        <v-data-table
          :headers="headers"
          :items="stockInfo"
          hide-default-footer
          hide-default-header
          disable-sort
          class="elevation-0"
          no-data-text="暂无数据"
        >
          <template v-slot:header="{ props: { headers } }">
            <thead>
              <tr>
                <td v-for="item in headers" :key="item.value" align="center">
                  <div v-if="item.text === 'meta'"></div>
                  <div v-if="item.text !== 'meta'">
                    <v-btn
                      text
                      v-if="item.text !== ''"
                      @click="changeData(item.text)"
                      >{{ item.text }}
                    </v-btn>
                  </div>
                </td>
              </tr>
            </thead>
          </template>
        </v-data-table>
      </v-card>
    </v-row>
    <v-row align="center" style="justify-content: center; margin: 1%">
      <v-col>
        <v-row align="center" style="justify-content: center">
          <v-card width="100%">
            <v-card-text>
              <v-row>
                <v-col>
                  <v-row align="center" style="justify-content: center">
                    <v-chart
                      class="chart"
                      :option="option"
                      autoresize
                      style="min-height: 400px"
                    />
                  </v-row>
                  <v-divider></v-divider>
                  <v-row
                    align="center"
                    style="justify-content: center; margin-top: 1%"
                  >
                    <v-data-table
                      :headers="detailHeaders"
                      :items="detailInfo"
                      hide-default-footer
                      disable-sort
                      style="width: 100%"
                      no-data-text="暂无数据"
                    >
                      <template
                        v-for="(name, index) in detailStol"
                        v-slot:[`item.${name}`]="{ item }"
                      >
                        <Quartile
                          v-if="item.titleName === '行业四分位'"
                          :level="item[name]"
                          style="margin-top: 5%"
                          :key="index"
                        ></Quartile>
                        <div
                          v-if="item.titleName !== '行业四分位'"
                          :key="index"
                        >
                          {{ item[name] }}
                        </div>
                      </template>
                    </v-data-table>
                  </v-row>
                </v-col>
              </v-row>
            </v-card-text>
          </v-card>
        </v-row>
      </v-col>
    </v-row>
  </div>
</template>
<script>
import { use } from "echarts/core";
import { CanvasRenderer } from "echarts/renderers";
import Quartile from "@/components/Quartile.vue";
import VChart from "vue-echarts";
import {
  TitleComponent,
  TooltipComponent,
  LegendComponent,
} from "echarts/components";
import { getFactor, getFactorInfo } from "@/api/compare_api";
import { initHeaders, cleanMeta, ObjToArray } from "@/utils";
import { notify } from "@/components/notification";

use([CanvasRenderer, TitleComponent, TooltipComponent, LegendComponent]);
export default {
  components: { Quartile, VChart },
  props: {
    info: {
      time: "",
    },
  },
  methods: {
    init() {
      this.setEmpty();
      this.id = this.$store.state.item.stock_id;
      this.selected = this.$store.state.selected;

      this.setFactorInfo(this.id, this.info.time);
    },
    setFactorInfo(id, time) {
      let info = "";
      if (this.info.time === "") {
        info = id;
        return
      } else {
        info = `${id}/${time}`;
      }
      getFactorInfo(info)
        .then((res) => {
          if (res.data.status === 200) {
            let headers = cleanMeta(res.data.headers);
            initHeaders(headers, this);
            this.stockInfo = res.data.data;
            this.$set(this, "stockInfo", res.data.data);
            this.setFactor(this.headers[1].text, this.id, this.info.time);
            notify(
              "success",
              this.$store.state.item.headers1 + "场景因子比较信息加载成功"
            );
          } else {
            this.setEmpty();
            notify("error", this.$store.state.item.headers1 + "暂无消息");
          }
        })
        .catch();
    },
    setFactor(factor, id, time) {
      let info = "";
      if (this.info.time === "") {
        info = id;
      } else {
        info = `${id}/${time}`;
      }
      getFactor(factor, info)
        .then((res) => {
          if (res.data.status === 200) {
            let headers = cleanMeta(res.data.headers);
            initHeaders(headers, this, [], "detailHeaders");
            for (let i = 0; i < headers.length; i++) {
              this.detailStol.push(headers[i]);
            }
            let name = this.$store.state.item.headers1;
            this.option.yAxis.pop();
            this.option.yAxis.push({
              type: "value",
              name: factor,
              axisLabel: {
                formatter: "{value}",
              },
            });
            this.$set(this.option.legend, "data", [
              name,
              "行业均值",
              "行业中值",
            ]);
            this.$set(
              this.option.xAxis[0],
              "data",
              headers.slice(1, headers.length)
            );
            let self = ObjToArray(res.data.data[0], headers);
            let industryMean = ObjToArray(res.data.data[1], headers);
            let industryMedian = ObjToArray(res.data.data[2], headers);
            let industryRank = ObjToArray(res.data.data[3], headers);
            this.setEChartsAndDetailTable(
              self,
              industryMean,
              industryMedian,
              industryRank
            );
          } else {
            this.setColEmpty();
            notify("error", "暂无数据");
          }
        })
        .catch();
    },
    setEChartsAndDetailTable(self, industryMean, industryMedian, industryRank) {
      this.setEchartsData(
        self.slice(1, self.length),
        industryMean.slice(1, self.length),
        industryMedian.slice(1, self.length)
      );
      this.setDetailTableData(self, industryMean, industryMedian, industryRank);
    },
    setEchartsData(self, industryMean, industryMedian) {
      this.$set(this.option.series[0], "data", self);
      this.$set(this.option.legend, "data[0]", this.$store.state.item.headers1);
      this.$set(this.option.series[0], "name", this.$store.state.item.headers1);
      this.$set(this.option.series[1], "data", industryMean);
      this.$set(this.option.series[2], "data", industryMedian);
      this.$set(this.option.graphic, "invisible", true);
    },
    setDetailTableColData(key, value) {
      for (let i = 0; i < value.length; i++) {
        let item = this.detailHeaders[i];
        this.$set(key, item.value, value[i]);
      }
    },
    setDetailTableData(self, industryMean, industryMedian, industryRank) {
      this.setDetailTableColData(this.detailInfo[0], self);
      this.setDetailTableColData(this.detailInfo[1], industryMean);
      this.setDetailTableColData(this.detailInfo[2], industryMedian);
      this.setDetailTableColData(this.detailInfo[3], industryRank);
      let quartiles = [];
      quartiles.push("行业四分位");
      for (let i = 0; i < industryRank.length; i++) {
        let ranks = industryRank[i].split("|");
        let q = parseFloat(ranks[0]) / parseFloat(ranks[1]);
        if (!Number.isNaN(q)) {
          let quartile = 4;
          if (q <= 0.25) {
            quartile = 4;
          } else if (q <= 0.5) {
            quartile = 3;
          } else if (q <= 0.75) {
            quartile = 2;
          } else {
            quartile = 1;
          }
          quartiles.push(quartile);
        }
      }
      this.setDetailTableColData(this.detailInfo[4], quartiles);
    },
    changeData(headerName) {
      this.setFactor(headerName, this.id, this.info.time);
    },
    setEmpty() {
      this.$set(this, "headers", []);
      this.$set(this, "stockInfo", []);
      this.$set(this, "detailHeaders", []);
      this.$set(this, "detailInfo", [
        {},
        {},
        {},
        {},
        { titleName: "行业四分位" },
      ]);
      let option = JSON.parse(JSON.stringify(this.noDataOption));
      this.$set(this, "option", option);
    },
    setColEmpty() {
      this.$set(this, "detailHeaders", []);
      this.$set(this, "detailInfo", [
        {},
        {},
        {},
        {},
        { titleName: "行业四分位" },
      ]);
      let option = JSON.parse(JSON.stringify(this.noDataOption));
      this.$set(this, "option", option);
    },
  },
  mounted() {
    this.init();
  },
  created() {
  },
  beforeCreate() {},
